<template>
    <div id="content-wrapper" class="container wrapper">

        <div class="blog-content col-md-8">
            <PostPreview
                v-for="post in posts"
                :key="post.id"
                :title="post.title"
                :excerpt="post.summary"
                :thumbnail-image="post.featured_image"
                :slug="post.slug" 
                :created="post.created"
            />      
        </div>
        <SidePanelCategoriesTags
        :categories="categories"
        :tags="tags" />

        </div>
</template>


<script>
import PostPreview from "@/components/PostPreview"
import SidePanelCategoriesTags from "@/components/SidePanelCategoriesTags"
import { butter } from "@/plugins/buttercms"

export default {
  name: 'Home',
  components: {
    PostPreview,
    SidePanelCategoriesTags
  },
  data () {
    return {
        posts: [],
        categories: [],
        tags: []
    }
  },
  methods: {
      getLatestPosts() {
          butter.post.list(
            {
                page: 1,
                page_size: 3,
                //tag_slug: this.$route.path.substring(1,3)
                tag_slug: 'en'
            }
            ).then((res) => {this.posts = res.data.data})
      },
      getCategories() {
          butter.category.list(
            {include: 'recent_posts'}
        ).then((res) => { 
            this.categories = res.data.data.map(bp => {
        return {
          slug: bp.slug,
          name: bp.name,
          posts: bp.recent_posts
        }
      })
         })
      },
      getTags() {
          butter.tag.list(
      {include: 'recent_posts'}
    ).then((res) => { 
        this.tags = res.data.data.map(bp => {
        return {
          slug: bp.slug,
          name: bp.name,
          total: bp.recent_posts.length
        }
      })
     })
      }
  },
  mounted() {
      this.getLatestPosts()
      this.getCategories()
      this.getTags()
  }
};
</script>
