<template>
    <aside id="primary-sidebar" class=" col-md-4 clearfix">

 
        <!-- Widget Category
        ============================================= -->
        <div class="category-widget widget">
            <h3 class="widget-title title-link">Categories</h3>
            <ul class="widget-item">
                <li v-for="cat in categories" :key="cat.id">
                    <router-link :to="{name: 'category-posts', params: { category: cat.slug }}">{{cat.name}}</router-link>
                </li>
            </ul>
        </div>
        <!-- end of widget category -->

        <!-- Widget Tags
        ============================================= -->
        <div class="tags-widget widget">
            <h3 class="widget-title title-link">Tags</h3>
            <router-link v-for="tag in tags_with_no_i18n" :key="tag.slug" :to="{name: 'tag-posts', params: {tag: tag.slug}}">{{ tag.name }}</router-link>
        </div>
        <!-- end of tags -->

    </aside>
</template>

<script>
export default {
    props: {
    categories: {
      type: Array,
      default: () => []
    },
    tags: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    tags_with_no_i18n: function() {
      return this.tags.filter(function(tag){
        if (tag.name != 'en' && tag.name != 'br'){
          return tag;
        }
      })
    }
  },
  methods: {
    category_total_posts_no_i18n: function(posts) {
      let i = 0
      let lang = this.$route.path.substring(1,3)
      posts.forEach(element => {
        if(element.tags.some(o => o.name === lang)){
          i++
        }
      });
      return i
    }
  }
}
</script>
